* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.weather {
  max-width: 450px;
  background-color: #7a956b;
  border-radius: 10px;
  margin: 10px auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .3);
}

.weather__loading {
  justify-content: center;
  align-items: center;
  display: flex;
}

.weather__loading img {
  max-width: 50px;
}

.weather__header {
  display: flex;
}

.weather__main {
  flex: auto;
}

.weather__city {
  margin-bottom: 10px;
  font-size: 40px;
  font-weight: 700;
}

.weather__date {
  font-size: 30px;
  font-weight: 500;
}

.weather__status {
  margin-bottom: 10px;
  font-size: 30px;
}

.weather__body {
  display: flex;
}

.weather__temp {
  margin-bottom: 10px;
  font-size: 30px;
  font-weight: 700;
}

